import React from 'react'
import './style.css'

function OrderBy({ map, state }) {
    const [backendTypes, backendStyles, backendTags] = map
    const [setStateULR] = state

    return (
        <div className="OrderBy">
            <select onChange={e => setStateULR(state => ({ ...state, type: e.target.value, offset: 1 }))}>
                <option value={-1}>Selecione um Tipo</option>
                {backendTypes.map((popsition, indice) =>
                    <option key={indice} value={popsition.id}>{popsition.name}</option>
                )}
            </select>

            <select onChange={e => setStateULR(state => ({ ...state, style: e.target.value, offset: 1 }))}>
                <option value={-1}>Selecione um Estilo</option>
                {backendStyles && backendStyles.map((popsition, indice) =>
                    <option key={indice} value={popsition.id}>{popsition.name}</option>
                )}
            </select>

            <select onChange={e => setStateULR(state => ({ ...state, tags: e.target.value, offset: 1 }))}>
                <option value={-1}>Selecione uma Tag</option>
                {backendTags && backendTags.map((popsition, indice) =>
                    <option key={indice} value={popsition.id}>{popsition.name}</option>
                )}
            </select>
        </div >
    )
}

export default OrderBy