import { React } from 'react';
import { Switch, Route } from 'react-router-dom'

import { MidiaPage } from './pages/Midia/MidiaPage'
// import { MidiaCliente } from './pages/Midia/CltMediaPage'
import { MidiaView } from './pages/Midia/MidiaView'
// import { ClienteMidiaView } from './pages/Midia/CltMidiaView'

import { ProductsPage } from './pages/Produtos/ProductsPage';
import { ProductView } from './pages/Produtos/ProductsView'
import { ProdutoCliente } from './pages/Produtos/CltProdutoPage'
import { ProductViewCliente } from './pages/Produtos/CltProductsView'

import { BookPage } from './pages/Book/BookPage';
import { BookView } from './pages/Book/BookView'
import { BookCliente } from './pages/Book/CltBookPage'
import { ClienteBookView } from './pages/Book/CltBookView'

import { Login } from './pages/Login'
import { Register } from './pages/Register'
import { ForgotPassword } from './pages/ForgotPassword'
import { NewPassword } from './pages/NewPassword'
import { AccessType } from './pages/AccessType'

function Routes() {
    return (
        <Switch>
            <Route exact path='/' component={AccessType} />
            <Route path='/login' component={Login} />W
            <Route path='/register' component={Register} />
            <Route path='/forgotpassword' component={ForgotPassword} /> 
            <Route path='/newpassword' component={NewPassword} />

            <Route path='/midia' component={MidiaPage} />
            {/* <Route path='/cltmidia' component={MidiaCliente} /> */}
            <Route path='/midiaview' component={MidiaView} />
            {/* <Route path='/cltmidiaview' component={ClienteMidiaView} /> */}

            <Route path='/produtos' component={ProductsPage} />
            <Route path='/cltprodutos' component={ ProdutoCliente } />
            <Route path='/produtosview' component={ ProductView } />
            <Route path='/cltprodutosview' component={ ProductViewCliente } />

            <Route path='/book' component={BookPage} />
            <Route path='/cltbook' component={BookCliente} />
            <Route path='/bookview' component={BookView} />
            <Route path='/cltbookview' component={ClienteBookView} />

        </Switch>
    )
}

export default Routes;