import React, { useEffect, useState } from 'react'
import './style.css'

function Search({ data }) {
  const [DataBase, setData] = data
  const [valueSearch, setValueSearch] = useState(-1)

  const filter = (valueFilter, DataBase, setData) => {
    let newData = DataBase

    if (valueFilter !== -1) {
      newData = DataBase.filter(valueData =>
        valueData.name.toLowerCase().includes(valueFilter.toLowerCase()))
    }
    setData(newData)
  }

  useEffect(() => {
    if (valueSearch === '') {
      setValueSearch(-1)
    }

    filter(valueSearch, DataBase, setData)
  }, [valueSearch, DataBase, setData])

  return (
    <div className="Search">
      <form action="">
        <input
          type="text"
          id="txtBusca"
          placeholder="Buscar..."
          onChange={(e) => setValueSearch(e.target.value)}
        />
        <button onClick={(e) => e.preventDefault()}>
          <img src="./assets/images/search.png" id="btnBusca" alt="Buscar" />
        </button>
      </form>
    </div>
  )
}

export default Search