import React from 'react'

import "./style.css"

const BookProduto = ({ src, title }) => {
  const medias = src
  return (
    <div className="BookProduto">

      <div className="BookCover">
        <img src={medias[2] ? medias[2].image : './assets/images/MidiaNaoEncontrada.svg'} alt="" className="ImgOne" />
        <img src={medias[1] ? medias[1].image : './assets/images/MidiaNaoEncontrada.svg'} alt="" className="ImgTwo" />
        <img src={medias[0] ? medias[0].image : './assets/images/MidiaNaoEncontrada.svg'} alt="" className="ImgThree" />
        <div className="ItensNumber">
          <span>
            {
              medias.length < 10
                ? `0${medias.length}`
                : medias.length
            }
          </span>
        </div>
      </div>

      <p>{title}</p>
    </div >
  )
}

export default BookProduto