import React from 'react'
import './style.css'

export const ImgRender = (props) => {
  let image = props.data

  return (
    <img
      className='ImgRender'
      id={image.id}
      key={image.id}
      src={image.image}
      width='50px' height='70px'
      onClick={props.func ? props.func : null}
      alt="Midia for Book"
    />
  )
}