import React, { useState } from "react";
import axios from "axios";

import './styles.css';


function CreateNewUser(name, sobrenome, email, senha, senha2) {
    if (senha === senha2) {
        const baseURL = 'http://127.0.0.1:8000/users/create/'
        const config = { headers: { 'Content-Type': 'multipart/form-data' } }

        let datas = new FormData()

        datas.append('first_name', name)
        datas.append('surname', sobrenome)
        datas.append('email', email)
        datas.append('password', senha)

        axios
            .post(baseURL, datas, config)
            .then((res) => {
                console.log(res.data)
                window.location.href = "/login";
            })
            .catch((err) => console.log("Erro: " + err))

    } else {
        alert("senha Diferentes")
    }
}

export const Register = () => {
    const [name, setName] = useState('')
    const [sobrenome, setSobrenome] = useState('')
    const [email, setEmail] = useState('')
    const [senha, setSenha] = useState('')
    const [senha2, setSenha2] = useState('')

    return (
        <div id="page-register">
            <div className="container">

                <div className="logo-container">
                    <img src="./assets/images/logo-vertical.png" alt="Logo Greenish" />
                </div>


                <form className="form-container">
                    <div className="user-name">
                        <div className="input-block">
                            <input
                                type="firt-name"
                                id="first-name"
                                placeholder="Nome"
                                onChange={(e) => setName(e.target.value)}
                            />
                        </div>

                        <div className="input-block">
                            <input
                                type="last-name"
                                id="last-name"
                                placeholder="Sobrenome"
                                onChange={(e) => setSobrenome(e.target.value)}
                            />
                        </div>

                    </div>
                    <div className="input-block">
                        <input
                            type="email"
                            id="email"
                            placeholder="Email"
                            onChange={(e) => setEmail(e.target.value)}
                        />
                    </div>

                    <div className="input-block">
                        <input
                            type="password"
                            id="password"
                            placeholder="Senha"
                            onChange={(e) => setSenha(e.target.value)}
                        />
                    </div>

                    <div className="input-block">
                        <input
                            type="password"
                            id="password2"
                            placeholder="Confirmar Senha"
                            onChange={(e) => setSenha2(e.target.value)}
                        />
                    </div>

                </form>

                <div className="button-container">
                    <button
                        className="button"
                        onClick={() => { CreateNewUser(name, sobrenome, email, senha, senha2) }}
                    >
                        Cadastrar-se
                    </button>
                </div>

            </div>
        </div>
    )
}
