import React, { useEffect, useState } from 'react'
import Select from 'react-select'
import makeAnimated from 'react-select/animated';


import './style.css'

import { SelectionMidia } from '../SelectionMidia';
import { MultiSelect } from '../../MultiSelect';
import { ImgRender } from '../../ImgRender';
import { ModalBase } from '../ModalBase';
import api from '../../../services/api';

const customStyles = {
  option: () => ({
    borderBottom: '1px dotted gray',
    color: '#5f5f5f',
    padding: 15,
    fontSize: 20,
    cursor: 'pointer'
  }),
  control: (provided, state) => ({
    ...provided,
    fontSize: 55,
    Width: 520,
    maxWidth: 520,
    Height: 30,

    display: 'flex',
    borderRadius: 6,
    border: 1,
    boxShadow: state.isFocused ? "0px 0px 2px 1px black" : "0px 0px 2px 1px gray"
  }),
  input: (provided) => ({
    ...provided,
    fontSize: 30,
  }),
  noOptionsMessage: (provided) => ({
    ...provided,
    fontSize: 18,
  }),
}

const animatedComponents = makeAnimated();

async function AddNewProduct(name, categoria, subcategoria, colecao, produtoPrincipal, ColorMidia) {
  let MidiaCor = ColorMidia ? ColorMidia.map(color => {
    let ColorMidias = color.imgs.map(img => img.id)
    return {
      ColorId: color.id,
      ColorMidias: ColorMidias
    }
  }) : null

  let datas = {
    "name": name,
    "media": produtoPrincipal,
    "category": categoria,
    "collection": colecao,
    "subcategory": subcategoria
  }

  api
    .post('/products/admin/', datas)
    .then((res) => {
      console.log(res.data)
      MidiaCor && AddCorlorsVariations(res.data.id, MidiaCor)
      !MidiaCor && setTimeout(() => { window.location.reload() }, 500);
    })
    .catch((err) => console.log("Erro: " + err))
}

async function AddCorlorsVariations(idProduto, CorlorImages) {
  let completed = await CorlorImages.map((dataCorlors, index) => {
    let datas = {
      "product": idProduto,
      "color": dataCorlors.ColorId,
      "media": dataCorlors.ColorMidias
    }

    api
      .post('/products/admin/createvariation/', datas)
      .then((res) => { console.log(res.data) })
      .catch((err) => console.log("Seguinte erro: " + err))


    if (index === dataCorlors.length) {
      return true
    }
    return false
  })
  completed && setTimeout(() => { window.location.reload() }, 500);
}

export const ModalAddProduto = (props) => {
  const [data, setData] = useState(null)
  const [value, setValue] = useState([])

  const [indexSlider, setIndexSlider] = useState(0)

  const [name, setName] = useState('')
  const [categoria, setCategoria] = useState(-1)
  const [colecao, setColecao] = useState(-1)
  const [subcategoria, setSubcategoria] = useState(-1)

  const [color, setColor] = useState([])

  const [backendCategories, setBackendCategories] = useState(null)
  const [backendSubcategories, setBackendSubcategories] = useState(null)
  const [backendCollection, setBackendCollection] = useState(null)

  useEffect(() => {
    async function fetchCategories() {
      try {
        let response = await api.get("/properties/product/categories/")
        setBackendCategories(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    async function fecthSubcategories() {
      try {
        let response = await api.get("/properties/product/subcategories/")
        setBackendSubcategories(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    async function fetchCollection() {
      try {
        let response = await api.get("/properties/product/collection/")
        setBackendCollection(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    fetchCollection()
    fecthSubcategories()
    fetchCategories()
  }, [])

  useEffect(() => {
    async function getColors() {
      try {
        const response = await api.get("/colors/")
        response.data.map(cor =>
          setColor(color => [...color, { id: cor.id, value: cor.name, label: cor.name, imgs: [] }])
        )
      } catch (error) {
        console.error(error);
      }
    }
    getColors()
  }, [])

  const clearValues = () => {
    setName('')
    setCategoria(-1)
    setColecao(-1)
    setSubcategoria(-1)
    setData(null)
    setValue([])
    color.map(color => color.imgs = [])
  }

  return (
    <ModalBase title="Produto" subtitle='Adicionar Produto' clearValues={clearValues}>
      <div className="ModalAddProduto">
        <div className="Content">
          <p>Informações</p>
          <div className="InfoData">
            <input
              type="text"
              value={name}
              placeholder="Nome"
              onChange={e => setName(e.target.value)}
            />

            <select
              onChange={e => setCategoria(e.target.value)}
              value={categoria}
            >
              <option value={-1}>Selecione uma Categoria</option>
              {backendCategories && backendCategories.map((category, index) => {
                return (
                  <option key={index} value={category.id}>{category.name}</option>
                )
              })}
            </select>

            <select
              onChange={e => setColecao(e.target.value)}
              value={colecao}
            >
              <option value={-1}>Selecione uma Coleção</option>
              {backendCollection && backendCollection.map((category, index) => {
                return (
                  <option key={index} value={category.id}>{category.name}</option>
                )
              })}
            </select>

            <select
              onChange={e => setSubcategoria(e.target.value)}
              value={subcategoria}
            >
              <option value={-1}>Selecione uma Subcategoria</option>
              {backendSubcategories && backendSubcategories.map((category, index) => {
                return (
                  <option key={index} value={category.id}>{category.name}</option>
                )
              })}
            </select>

          </div>
          <div className='SelectionForms'>
            <Select
              name="colors"
              value={data}
              placeholder="Cor"
              onChange={e => setData(e)}
              isMulti
              components={animatedComponents}
              options={color}
              styles={customStyles}
              closeMenuOnSelect={true}
            />
          </div>

          <p>Produto</p>
          <div className="ProdutoData">
            <div className="Area-ViewsMedias">
              {
                value &&
                value.map((image, index) => {
                  return (
                    <ImgRender
                      key={image.id}
                      data={image}
                      func={
                        () => {
                          data[indexSlider].imgs.push(image)
                          setData([...data])
                          value.splice(index, 1)
                          setValue([...value])
                        }}
                    />
                  )
                })
              }
            </div>
            <div className="Area-SelectMidias">
              <SelectionMidia dataa={[value, setValue]} />
            </div>
          </div>
          <div style={{ height: '20rem', marginTop: '1.5rem', maxWidth: 530 }}>
            <MultiSelect
              values={[data, setData]}
              indexToSlider={[indexSlider, setIndexSlider]}
              dataReturn={[value, setValue]}
            />
          </div>
        </div>


        <div className="Footer">
          <button onClick={() => clearValues()}>
            Limpar
          </button>

          <button
            onClick={() => {
              let midias = value.map(id => {
                return id.id
              })
              AddNewProduct(name, categoria, subcategoria, colecao, midias, data)
            }}
          >
            Salvar</button>
        </div>
      </div>
    </ModalBase>
  )
}