import React from 'react'
import './style.css';

export const ForgotPassword = () => {
    return (
            <div className="ForgotContainer">
                <div className="ForgotContent">
                    <div className="LogoContainer">
                        <img src="./assets/images/logo-vertical.png" alt="Logo Greenish" />
                    </div>

                    <div className="FormContent">
                        <h1>
                            Perdeu a sua senha?
                        </h1>
                        <span>
                            Insira seu email para que possamos recuperar sua senha!
                        </span>

                        
                        <input type="email" id="email" placeholder="Email"/>

                        <a href="./" className="button">Recuperar</a>

                    </div>

                </div>
            </div>
    )
}