import React, { useEffect, useState } from 'react'

import './style.css'

import { ModalBase } from '../ModalBase';
import api from "../../../services/api"
import Select from 'react-select'

const customStyles = {
  option: () => ({
    borderBottom: '1px dotted gray',
    color: 'gray',
    padding: 15,
    fontSize: 16,
  }),
  control: (provided, state) => ({
    ...provided,
    fontSize: 40,
    maxWidth: 280,
    maxHeight: 30,
    overflowY: 'scroll',
    borderRadius: 8,
    overflow: 'hidden',
    border: 1,
    boxShadow: state.isFocused ? "0px 0px 2px 1px black" : "0px 0px 2px 1px gray"
  }),
  input: (provided) => ({
    ...provided,
    fontSize: 30,
    display: 'flex',
    '&:active': {
      border: '5px solid red',
    }
  }),
  noOptionsMessage: (provided) => ({
    ...provided,
    fontSize: 16,
  }),
}

const UpdateMidia = (tipo, estilo, tags, isPrivate, props) => {
  let newTags = tags.map((tag) => {
    return tag.id
  })
  let datas = {
    "media_type": tipo,
    "style": estilo,
    "tags": newTags,
    "is_private": isPrivate
  }
  api.patch(`/media/admin/${props.id}/`, datas)
    .then((response) => {
      console.log(response)
    })
    .catch((err) => console.log("Erro: " + err))
    .then(setTimeout(() => window.location.reload(), 500))
}

export const ModalEditMedia = ({ props }) => {
  let tagsIDs = props.tags.map((tag) => {
    return { id: tag.id, value: tag.name, label: tag.name }
  })

  const [tipo, setTipo] = useState(props.media_type ? props.media_type.id : 0)
  const [estilo, setEstilo] = useState(props.style ? props.style.id : 0)
  const [tags, setTags] = useState(tagsIDs)
  const [isPrivate, setIsPrivate] = useState(props.is_private)

  const [backendTypes, setBackendTypes] = useState(null)
  const [backendStyle, setBackendStyle] = useState(null)
  const [backendTags, setBackendTags] = useState([])

  useEffect(() => {
    async function fetchType() {
      try {
        let response = await api.get("/properties/media/type/")
        setBackendTypes(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    async function fetchStyle() {
      try {
        let response = await api.get("/properties/media/style/")
        setBackendStyle(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    async function fetchTags() {
      try {
        let response = await api.get("/tags/")
        response.data.map((tag) => setBackendTags(backendTags =>
          [...backendTags, { id: tag.id, value: tag.name, label: tag.name }])
        )

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    fetchType()
    fetchStyle()
    fetchTags()
  }, [])

  return (
    <ModalBase title="Editar" subtitle='Editar Mídia' color={"#fff"}>
      <div className="ModalEditMidia">
        <div className="Img-Selection">
          <div className="Img-Views">
            <img src={props.image} alt={`midia ID: ${props.image.id}`} />
          </div>

        </div>
        <div className="ValueData">
          <select
            id="appearance-select"
            name="TypeSelect"
            onChange={e => setTipo(e.target.value)}
            defaultValue={Number(tipo)}
          >
            <option value={0} disabled>Selecione um Tipo</option>
            {backendTypes && backendTypes.map((type, index) => {
              return (
                <option key={index} value={type.id}>{type.name}</option>
              )
            }
            )}
          </select>

          <select
            name="StyleSelect"
            onChange={e => setEstilo(e.target.value)}
            defaultValue={Number(estilo)}
          >
            <option value={0} disabled>Selecione um Estilo</option>
            {backendStyle && backendStyle.map((style, index) => {
              return (
                <option key={style.id} value={style.id}>{style.name}</option>
              )
            })}
          </select>

          <Select
            options={backendTags}
            defaultValue={backendTags.filter(tag => {
              return tags.map(tagg => tagg.id).includes(tag.id)
            })}
            placeholder="Selecione suas tags"
            isMulti
            styles={customStyles}
            name="Tags"
            onChange={(e) => {
              setTags(e)
            }}
          />

          <div className="checkbox">
            <input
              type="checkbox"
              name="Privado"
              onChange={(e) => { setIsPrivate(!isPrivate) }}
              checked={isPrivate}
            />
            <p>Privado</p>
          </div>

          <button onClick={() => {
            api.delete(`/media/admin/${props.id}/`)
              .then(setTimeout(() => window.location.assign('/midia'), 500))
          }} >
            Excluir
          </button>

          <button onClick={(e) => UpdateMidia(tipo, estilo, tags, isPrivate, props)}>
            Salvar
          </button>
        </div>
      </div>
    </ModalBase>
  )
}