import React from 'react'
import './style.css'

function Profile(props) {
return  (
    <div className="container">
        <div className="info">
            <span>
                <p>Bem vindo, { props.nome }</p>
                <p>{ props.email }</p>
            </span>
        </div>
    </div>
)
}

export default Profile