import React, { useState } from 'react'
import { ModalBase } from '../ModalBase';
import { SelectionMidia } from '../SelectionMidia';
import { ImgRender } from '../../ImgRender';
import api from '../../../services/api';

import './style.css'

function AddNewBook(data, name, description) {
  let imgsBook = data.map((value) => value.id)

  let datas = {
    "name": name,
    "description": description,
    "media": imgsBook,
  }

  api
    .post('/books/admin/', datas)
    .then((res) => {
      console.log(res)
      setTimeout(() => { window.location.reload() }, 500);
    })
    .catch((err) => console.log("Erro: " + err))
}

export const ModalAddBook = () => {
  const [value, setValue] = useState([])
  const [name, setName] = useState('')
  const [description, setDescription] = useState('')

  const clearValues = () => {
    setName('')
    setDescription('')
    setValue([])
  }

  return (
    <ModalBase title="Book" subtitle='Adicionar Book' clearValues={clearValues}>
      <div className="ModalAddBook">
        <div className="form">
          <div className="description" >
            <input
              type="text"
              placeholder="Nome do Book"
              value={name}
              onChange={(e) => { setName(e.target.value) }}
            />
            <textarea
              cols="30"
              rows="10"
              value={description}
              placeholder="Drescrição"
              onChange={(e) => { setDescription(e.target.value) }}
            />
          </div>

          <div className="MidiaSelection">
            <div className="Area-ViewsMedias">
              {
                value &&
                value.map(image => {
                  return (
                    <ImgRender
                      key={image.id}
                      data={image}
                    />
                  )
                })
              }
            </div>
            <div className="Area-SelectMidias">
              <SelectionMidia dataa={[value, setValue]} />
            </div>
          </div>

        </div>
        <div className="Footer">
          <button className="Limpar"
            onClick={() => clearValues()}
          >Limpar</button>
          <button className="Salvar" onClick={() => AddNewBook(value, name, description)}>Salvar</button>
        </div>

      </div>
    </ModalBase>
  )
}