import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import Pagination from '@material-ui/lab/Pagination';
import PaginationItem from '@material-ui/lab/PaginationItem';
import { makeStyles } from "@material-ui/core/styles";

import MenuCategoryClt from '../../../components/MenuCategoryClt'
import Search from '../../../components/Search';
import api,{api_address} from '../../../services/api';

import ImgProduto from '../../../components/ImgProduto'

import './styles.css'

const useStyles = makeStyles(() => ({
  root: {
    "& .Mui-selected": {
      background: "#00cc6d",
      color: "white",
      "&:hover": {
        background: "#c4f5d0",
        color: "black"
      }
    },
    "& .MuiPaginationItem-page:hover": {
      background: "#c4f5d0",
      opacity: 0.9
    }
  }
}));

const createURLtoFetch = (stateURL, setBaseToFetchMediaURL) => {
  let NewURL = `${api_address}/products/?`
  if (stateURL.limit !== -1) {
    NewURL += `limit=${stateURL.limit}`
  }
  if (stateURL.offset !== -1) {
    NewURL += `&offset=${(Number(stateURL.offset) - 1) * stateURL.limit}`
  }
  setBaseToFetchMediaURL(NewURL)
}

export const ProdutoCliente = () => {
  const [BaseToFetchMediaURL, setBaseToFetchMediaURL] = useState(null)

  const [data, setData] = useState(null)
  const [DataBase, setDataBase] = useState(null)

  const [tamanhoDaPaginação, setTamanhoDaPaginação] = useState(0)

  const [stateURL, setStateULR] = useState({
    limit: 8,
    offset: 1,
  })

  useEffect(() => createURLtoFetch(stateURL, setBaseToFetchMediaURL), [stateURL])

  useEffect(() => {
    const fetchMediaDataEndPagination = async (MidiaPagination) => {
      try {
        let response = await api.get(MidiaPagination)
        response = response.data

        if ((response.count % stateURL.limit) > 0) {
          setTamanhoDaPaginação(Math.floor(response.count / stateURL.limit) + 1)
        } else {
          setTamanhoDaPaginação(Math.floor(response.count / stateURL.limit))
        }

        setDataBase(response.results)
        setData(response.results)
      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    BaseToFetchMediaURL && fetchMediaDataEndPagination(BaseToFetchMediaURL)
  }, [BaseToFetchMediaURL])

  const classes = useStyles();

  return (
    <div className="ProdutoCliente">
      <div className="Container">
        <div className="Top-Menu">
          <div className="CategoryMenu">
            <MenuCategoryClt title="Produto" />
            <div className="Search">
              {DataBase && <Search data={[DataBase, setData]} />}
            </div>
          </div>
        </div>

        <div className="Content-App">
          <div className="content-midia-views">
            {data &&
              data.map((produto, index) => (
                <Link key={index} to={`/cltprodutosview?productID=${produto.id}`}>
                  <ImgProduto
                    key={produto.id}
                    src={produto.media[0]
                      ? produto.media[0].image
                      : './assets/images/SemMidia.svg'
                    }
                    title={produto.name}
                  />
                </Link>
              ))
            }
          </div>
          <div className="Content-Pagination">
            <Pagination
              onChange={(_, v) => setStateULR(state => ({ ...state, offset: v }))}
              page={stateURL.offset}
              className={classes.root}
              count={tamanhoDaPaginação}
              boundaryCount={2}
              renderItem={(item) => (
                <PaginationItem
                  style={{
                    fontSize: 12,
                    fontWeight: 'bold',
                  }}
                  {...item} />
              )}
            />
          </div>
        </div>

      </div>

    </div>

  )
}