import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

// Import Swiper styles
import "swiper/swiper.min.css";
import "swiper/components/pagination/pagination.min.css"

import "./styles.css";


// import Swiper core and required modules
import SwiperCore, {
  Pagination
} from 'swiper/core';
import { ImgRender } from "../ImgRender";

// install Swiper modules
SwiperCore.use([Pagination]);


export const MultiSelect = (props) => {
  let [data, setData] = props.values
  let [indexSlider, setIndexSlider] = props.indexToSlider
  let [value, setValue] = props.dataReturn
  return (
    <>
      <Swiper
        spaceBetween={30}
        direction={"vertical"}
        pagination={{ "clickable": true }}
        className="mySwiper"
        observer='true'
        onSlideChange={(swiper) => setIndexSlider(swiper.activeIndex)}

      >
        {data && data.map((valor, index) => {
          return (

            <SwiperSlide key={index}>
              <h4
                style={{
                  background: '#f1f1f1',
                  display: 'flex',
                  fontSize: '20px',
                  paddingBottom: '1rem'
                }}
              >
                {valor.label}
              </h4>

              <div
                style={{
                  background: 'white',
                  border: '1px solid rgba(76, 76, 76, .5)',
                  borderRadius: '5px',
                  minHeight: '71.5%',
                  display: 'flex',
                  alignItems: 'center',
                  padding: '1rem',
                }}
              >
                {valor.imgs && valor.imgs.map((image, index) => {
                  return (
                    <ImgRender
                      key={image.id}
                      data={image}
                      func={
                        () => {
                          value.push(image)
                          setValue([...value])
                          data[indexSlider].imgs.splice(index, 1)
                          setData([...data])
                        }}
                    />
                  )
                })}
              </div>
            </SwiperSlide>

          )
        })}
      </Swiper>
    </>
  )
}