import React, { useEffect, useState } from 'react'

import './style.css'

import { ModalBase } from '../ModalBase';
import api, { AddNewMidia, fetchMediaStyles, fetchMediaType } from "../../../services/api"
import Select from 'react-select'

const customStyles = {
  option: () => ({
    borderBottom: '1px dotted gray',
    color: '#5f5f5f',
    padding: 15,
    fontSize: 18,
  }),
  control: (provided, state) => ({
    ...provided,
    fontSize: 55,
    maxWidth: 280,
    maxHeight: 40,
    overflowY: 'scroll',
    borderRadius: 6,
    overflow: 'hidden',
    border: 1,
    boxShadow: state.isFocused ? "0px 0px 2px 1px black" : "0px 0px 2px 1px gray"
  }),
  input: (provided) => ({
    ...provided,
    fontSize: 12,
    display: 'flex',
    '&:active': {
      border: '5px solid red',
    }
  }),
  noOptionsMessage: (provided) => ({
    ...provided,
    fontSize: 18,
  }),
}

export const ModalAddMidia = () => {
  const [images, setImages] = useState([])
  const [tipo, setTipo] = useState(-1)
  const [estilo, setEstilo] = useState(-1)
  const [tags, setTags] = useState(null)
  const [isPrivate, setIsPrivate] = useState(false)

  const [imgsPreview, setImgsPreview] = useState(null)

  const [backendTypes, setBackendTypes] = useState(null)
  const [backendStyle, setBackendStyle] = useState(null)
  const [backendTags, setBackendTags] = useState([])

  const clearValues = () => {
    setImages(null)
    setTipo(-1)
    setEstilo(-1)
    setTags(null)
    setIsPrivate(false)
    setImgsPreview(null)
  }

  useEffect(() => {
    fetchMediaType(setBackendTypes)
    fetchMediaStyles(setBackendStyle)
    async function fetchTags() {
      try {
        let response = await api.get("/tags/")
        response.data.map((tag) => setBackendTags(backendTags =>
          [...backendTags, { id: tag.id, value: tag.name, label: tag.name }])
        )

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }
    fetchTags()
  }, [])

  const PreviewImgs = async (e) => {
    const files = e.target.files
    let ListBase64 = []

    for (let i = 0; i < files.length; i++) {
      let ValueBase64 = await convertBase64(files[i])
      ListBase64.push(ValueBase64)
    }
    setImgsPreview(ListBase64);
  }

  const convertBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);

      fileReader.onload = () => {
        resolve(fileReader.result);
      };

      fileReader.onerror = (error) => {
        reject(error);
      };
    });
  }

  return (
    <ModalBase title="Mídia" subtitle='Adicionar Mídia' clearValues={clearValues}>
      <div className="ModalAddMidia">
        <div className="Img-Selection">
          <div className="Img-Views">
            {imgsPreview && imgsPreview.map((img, index) =>
              <img src={img} key={index} alt={`midia ID: ${img.id}`} />
            )}
          </div>
          <div className="Img-InputSelection">
            <label>Selecionar Midias
              <input
                accept="image/*"
                type="file"
                multiple
                onChange={e => {
                  PreviewImgs(e)
                  setImages(Array.from(e.target.files))
                }}
              />
            </label>
          </div>
        </div>
        <div className="ValueData">
          <select
            id="appearance-select"
            name="TypeSelect"
            onChange={e => setTipo(e.target.value)}
            value={tipo}
          >
            <option value={-1}>Selecione um Tipo</option>
            {backendTypes && backendTypes.map((type, index) =>
              <option key={index} value={type.id}>{type.name}</option>
            )}
          </select>

          <select
            name="StyleSelect"
            onChange={e => setEstilo(e.target.value)}
            value={estilo}

          >
            <option value={-1}>Selecione um Estilo</option>
            {backendStyle && backendStyle.map((style, index) =>
              <option key={index} value={style.id}>{style.name}</option>
            )}
          </select>

          <Select
            options={backendTags}
            placeholder="Selecione suas tags"
            isMulti
            value={tags}
            styles={customStyles}
            name="Tags"
            onChange={(e) => setTags(e)}
          />

          <div className="checkbox">
            <input
              type="checkbox"
              name="Privado"
              onChange={(e) => { setIsPrivate(!isPrivate) }}
              checked={isPrivate}
            />
            <p>Privado</p>
          </div>

          <button onClick={() => clearValues()}>Limpar</button>
          <button onClick={() => {
            if (tipo == -1 || estilo == -1) {
              alert('Por Favor Selecione um tipo e um estilo para sua Midia.')
            } else {
              AddNewMidia(images, tipo, estilo, tags, isPrivate)
            }
          }} >Salvar</button>
        </div>
      </div>
    </ModalBase>
  )
}