import React, { useState } from 'react'
import { ModalBase } from '../Modals/ModalBase'

import "./styles.css"

const ModalViewMedia = props => (
  <ModalBase title={<img src={props.img} />} subtitle=''>
    <div className="ModalViewMedia">
      <img src={props.img} />
    </div>
  </ModalBase>
)

const ProdutoView = ({ data }) => (
  <div className="ImgProdutoView">
    <div className="PrincipalImg">
      {
        data.media.map((media, index) => (
          <ModalViewMedia key={index} img={media.image} />
          // <img
          //   key={index}
          //   src={media.image}
          //   alt={`ProductID: ${media.id}`}
          //   onClick={() => (<ModalMedia>{media.image}</ModalMedia>)}
          // />
        ))
      }
    </div>
    {
      data.color_variations.map(MediasColor => (
        <div key={MediasColor.id}>
          <span>{MediasColor.color}</span>
          <div className="Cor">
            {
              MediasColor.media.map((media, index) => (
                <ModalViewMedia key={index} img={media.image} />
                // <img key={media.id} src={media.image} alt="" />
              ))
            }
          </div>
        </div>
      ))
    }
  </div>
)

export default ProdutoView