import React, { useEffect, useState } from 'react'
import Select from 'react-select'
import makeAnimated from 'react-select/animated';

import './style.css'

import { SelectionMidia } from '../SelectionMidia';
import { MultiSelect } from '../../MultiSelect';
import { ImgRender } from '../../ImgRender';
import { ModalBase } from '../ModalBase';
import api, { AdminAccess } from '../../../services/api';

const customStyles = {
  option: () => ({
    borderBottom: '1px dotted gray',
    color: '#5f5f5f',
    padding: 15,
    fontSize: 20,
    cursor: 'pointer'
  }),
  control: (provided, state) => ({
    ...provided,
    fontSize: 55,
    Width: 515,
    maxWidth: 520,
    Height: 30,

    display: 'flex',
    borderRadius: 6,
    border: 1,
    boxShadow: state.isFocused ? "0px 0px 2px 1px black" : "0px 0px 2px 1px gray"
  }),
  input: (provided) => ({
    ...provided,
    fontSize: 30,
  }),
  noOptionsMessage: (provided) => ({
    ...provided,
    fontSize: 18,
  }),
}

const animatedComponents = makeAnimated();

const UpdateProduct = (name, produtoPrincipal, categoria, colecao, subcategoria, colorMidias, props) => {

  let IdsProdutoPrincipal = produtoPrincipal.map((media) => {
    return media.id
  })

  // let MidiaCor = colorMidias.map(color => {
  //   let ColorMidias = color.imgs.map(img => {
  //     return img.id
  //   })
  //   return {
  //     ColorId: color.id,
  //     ColorMidias: ColorMidias
  //   }
  // })

  let datas = {
    "name": name,
    "media": IdsProdutoPrincipal,
    "category": categoria,
    "collection": colecao,
    "subcategory": subcategoria,
  }

  api.patch(`/products/admin/${props.id}/`, datas)
    .then((response) => {
      UdpadeColorVariation(colorMidias, props)
    })
    .catch((err) => console.log("Erro: " + err))
  // .then(setTimeout(() => window.location.reload(), 500))
}

const UdpadeColorVariation = async (colorMidias, props) => {
  let suporteBackend = props.color_variations.map(color => color)
  let suporteFrontEnd = colorMidias.map(color => color.value)

  colorMidias.map(mediasColor => {
    let contains = suporteBackend.filter(x => x.color === mediasColor.value)

    if (contains.length !== 0) {
      let medias = mediasColor.imgs.map(img => img.id)

      api
        .patch(`/products/admin/variations/${contains[0].id}/`, { "media": medias })
        .then((res) => { })
        .catch((err) => console.log("Seguinte erro: " + err))
    } else {
      let medias = mediasColor.imgs.map(img => img.id)

      api
        .post('/products/admin/createvariation/', { "product": props.id, "color": mediasColor.id, "media": medias })
        .then((res) => { })
        .catch((err) => console.log("Seguinte erro: " + err))
    }

  })

  props.color_variations.map(valueBack => {
    if (!suporteFrontEnd.includes(valueBack.color)) {
      api
        .delete(`products/admin/variations/${valueBack.id}/`)
        .then((res) => { })
        .catch((err) => console.log("Seguinte erro: " + err))
    }
  })

  setTimeout(() => window.location.reload(), 500)
}


export const ModalEditProduct = ({ props }) => {
  AdminAccess()

  const [data, setData] = useState([])
  const [value, setValue] = useState(props.media)

  const [indexSlider, setIndexSlider] = useState(0)

  const [name, setName] = useState(props.name)
  const [categoria, setCategoria] = useState(props.category.id)
  const [colecao, setColecao] = useState(props.collection.id)
  const [subcategoria, setSubcategoria] = useState(props.subcategory.id)

  const [color, setColor] = useState([])

  const [backendCategories, setBackendCategories] = useState(null)
  const [backendSubcategories, setBackendSubcategories] = useState(null)
  const [backendCollection, setBackendCollection] = useState(null)

  useEffect(() => {
    props.color_variations.map((color) =>
      setData(data => [...data, { id: color.id, value: color.color, label: color.color, imgs: color.media }])
    )
  }, [])

  useEffect(() => {
    async function fetchCategories() {
      try {
        let response = await api.get("/properties/product/categories/")
        setBackendCategories(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    async function fecthSubcategories() {
      try {
        let response = await api.get("/properties/product/subcategories/")
        setBackendSubcategories(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    async function fetchCollection() {
      try {
        let response = await api.get("/properties/product/collection/")
        setBackendCollection(response.data)

      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }

    fetchCollection()
    fecthSubcategories()
    fetchCategories()
  }, [])

  useEffect(() => {
    async function getColors() {
      api.get('/colors/')
        .then((response) => {
          response = response.data

          response.map(cor => {
            let currentData = null
            for (let dado of props.color_variations) {
              if (cor.name == dado.color) {
                currentData = dado
                break
              }
            }
            if (currentData) {
              setColor(color => [...color, { id: cor.id, value: cor.name, label: cor.name, imgs: currentData.media }])
            } else {
              setColor(color => [...color, { id: cor.id, value: cor.name, label: cor.name, imgs: [] }])
            }
          })
        })
        .catch((err) => console.log("Erro: " + err))
    }
    getColors()
  }, [])


  return (
    <ModalBase title="Produto" subtitle='Adicionar Produto' color={"#fff"}>
      <div className="Modal-EditProduct">
        <div className="Content">
          <p>Informações</p>
          <div className="InfoData">
            <input
              type="text"
              placeholder="Nome"
              value={name}
              onChange={e => setName(e.target.value)}
            />

            <select
              defaultValue={categoria}
              onChange={e => setCategoria(e.target.value)}
            >
              <option>Selecione uma Categoria</option>
              {backendCategories && backendCategories.map((category, index) => {
                return (
                  <option key={index} value={category.id}>{category.name}</option>
                )
              })}
            </select>

            <select
              defaultValue={colecao}
              onChange={e => setColecao(e.target.value)}
            >
              <option>Selecione uma Coleção</option>
              {backendCollection && backendCollection.map((category, index) => {
                return (
                  <option key={index} value={category.id}>{category.name}</option>
                )
              })}
            </select>

            <select
              defaultValue={subcategoria}
              onChange={e => setSubcategoria(e.target.value)}
            >
              <option>Selecione uma Subcategoria</option>
              {backendSubcategories && backendSubcategories.map((category, index) => {
                return (
                  <option key={index} value={category.id}>{category.name}</option>
                )
              })}
            </select>
          </div>

          <div className='SelectionForms'>
            <Select
              name="colors"
              value={data}
              placeholder="Cor"
              onChange={e => setData(e)}
              isMulti
              components={animatedComponents}
              options={color}
              styles={customStyles}
              closeMenuOnSelect={true}
            />
          </div>

          <p>Produto</p>
          <div className="ProdutoData">
            <div className="Area-ViewsMedias">
              {
                value &&
                value.map((image, index) => {
                  return (
                    <ImgRender
                      key={index}
                      data={image}
                      func={
                        () => {
                          data[indexSlider].imgs.push(image)
                          setData([...data])
                          value.splice(index, 1)
                          setValue([...value])
                        }}
                    />
                  )
                })
              }
            </div>
            <div className="Area-SelectMidias">
              <SelectionMidia dataa={[value, setValue]} />
            </div>
          </div>
          <div style={{ height: '20rem', marginTop: '1.5rem', maxWidth: 530 }}>
            <MultiSelect values={[data, setData]} indexToSlider={[indexSlider, setIndexSlider]} dataReturn={[value, setValue]} />
          </div>
        </div>


        <div className="Footer">
          <button
            onClick={() => {
              api.delete(`/products/admin/${props.id}/`)
                .then(setTimeout(() => window.location.assign('/produtos'), 500))
            }}
          >Excluir</button>
          <button
            onClick={() => { UpdateProduct(name, value, categoria, colecao, subcategoria, data, props) }}
          >
            Salvar</button>
        </div>
      </div>
    </ModalBase>
  )
}