import React, { useState } from 'react'
import './styles.css'

import { AiFillCloseCircle } from 'react-icons/all';

export const ModalBase = (props) => {
  const [isVisible, setIsVisible] = useState(false)
  const coloração = props.color
  let colorBase = coloração ? coloração : "rgba(193, 192,189,50%)"

  return (
    <div>
      {/* Botão ativação de modal */}
      <button
        className="ButtonModal"
        onClick={() => setIsVisible(true)}
        style={{ color: colorBase }}
      >
        {props.title}
      </button>

      {/* Render Modal */}
      {isVisible ?
        (
          <div
            id='modal'
            className='ModalBase'
          // onClick={(e) => handleOutsideClick(e)}
          >
            <div className="ContainerModalBase">

              <div className="headerModalBase">
                <span>{props.subtitle}</span>

                <button onClick={() => {
                  props.clearValues && props.clearValues()
                  setIsVisible(false)
                }}>
                  <AiFillCloseCircle size={25} />
                </button>

              </div>

              <div className="Content" >
                {props.children}
              </div>

            </div>

          </div>
        )
        : null}

    </div >
  )
}