import React, { useState } from 'react';
import './style.css'
// import { ModalAddCor } from '../Modals/AddCor';
import { ModalAddMidia } from '../Modals/AddMidia';
import { ModalAddBook } from '../Modals/AddBook';
import { ModalAddProduto } from '../Modals/AddProduto';
// import { ModalAddTags } from '../Modals/AddTags';
// import { ModalAddCollection } from '../Modals/AddColecao';
// import { ModalAddType } from '../Modals/AddTipo';
// import { ModalAddStyle } from '../Modals/AddEstilo';

function Accordion(props) {
    const [isActive, setIsActive] = useState(false);

    return (
        <div className="Accordion">
            <div className="Title-Accordion" onClick={() => setIsActive(!isActive)}>
                <img src="./assets/images/plus.png" alt="Adicionar" />
                {props.title}
            </div>
            {
                isActive &&
                <div className="Content-Accordion">
                    <ModalAddProduto />
                    <ModalAddMidia />
                    <ModalAddBook />
                    {/* <ModalAddCor />
                    <ModalAddTags />
                    <ModalAddType />
                    <ModalAddStyle />
                    <ModalAddCollection /> */}
                </div>
            }
        </div>
    );
}

export default Accordion