import React, { useEffect, useState } from 'react'
import { Link } from "react-router-dom"
import Pagination from '@material-ui/lab/Pagination'
import { makeStyles } from "@material-ui/core/styles"
import PaginationItem from '@material-ui/lab/PaginationItem'

import './styles.css'

import MenuCategory from '../../../components/MenuCategory'
import AsideBar from '../../../components/AsideBar'
import OrderBy from '../../../components/OrderBy'
import NavBar from '../../../components/NavBar'
import api, {
  fetchMediaType,
  fetchMediaStyles,
  fetchMediaTags,
  AdminAccess,
  api_address
} from '../../../services/api'

const useStyles = makeStyles(() => ({
  root: {
    "& .Mui-selected": {
      background: "#00cc6d",
      color: "white",
      "&:hover": {
        background: "#c4f5d0",
        color: "black"
      }
    },
    "& .MuiPaginationItem-page:hover": {
      background: "#c4f5d0",
      opacity: 0.9
    }
  }
}))

const createURLtoFetch = (stateURL, setBaseToFetchMediaURL) => {
  let NewURL = `${api_address}/media/?`
  if (stateURL.limit !== -1) {
    NewURL += `limit=${stateURL.limit}`
  }
  if (stateURL.offset !== -1) {
    NewURL += `&offset=${(Number(stateURL.offset) - 1) * stateURL.limit}`
  }
  if (Number(stateURL.style) !== -1) {
    NewURL += `&style=${stateURL.style}`
  }
  if (Number(stateURL.type) !== -1) {
    NewURL += `&type=${stateURL.type}`
  }
  if (Number(stateURL.tags) !== -1) {
    NewURL += `&tags=${stateURL.tags}`
  }
  setBaseToFetchMediaURL(NewURL)
}

export const MidiaPage = () => {
  AdminAccess()

  const [BaseToFetchMediaURL, setBaseToFetchMediaURL] = useState(null)
  const [BackendMediaStyles, setBackendMediaStyles] = useState(null)
  const [BanckendMediaData, setBanckendMediaData] = useState(null)
  const [BackendMediaTypes, setBackendMediaTypes] = useState(null)
  const [BackendMediaTags, setBackendMediaTags] = useState(null)

  const [tamanhoDaPaginação, setTamanhoDaPaginação] = useState(0)

  const [stateURL, setStateULR] = useState({
    limit: 10,
    offset: 1,
    style: -1,
    type: -1,
    tags: -1,
  })

  useEffect(() => createURLtoFetch(stateURL, setBaseToFetchMediaURL), [stateURL])

  useEffect(() => {
    const fetchMediaDataEndPagination = async (MidiaPagination) => {
      try {
        let response = await api.get(MidiaPagination)
        response = response.data

        if ((response.count % stateURL.limit) > 0) {
          setTamanhoDaPaginação(Math.floor(response.count / stateURL.limit) + 1)
        } else {
          setTamanhoDaPaginação(Math.floor(response.count / stateURL.limit))
        }
        setBanckendMediaData(response.results)
      } catch (err) {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      }
    }
    BaseToFetchMediaURL && fetchMediaDataEndPagination(BaseToFetchMediaURL)
  }, [BaseToFetchMediaURL])

  useEffect(() => {
    fetchMediaTags(setBackendMediaTags)
    fetchMediaStyles(setBackendMediaStyles)
    fetchMediaType(setBackendMediaTypes)
  }, [])

  const classes = useStyles();
  return (
    <div className="LandingPage">
      <AsideBar className="aside-bar-web" />
      <div className="Container">
        <div className="Top-Menu">
          <div className="CategoryMenu">
            <MenuCategory title="Midia" />
            <div className="sidebar">
              <NavBar />
            </div>
          </div>
          <div className="OrderMenu">
            {BackendMediaTypes &&
              <OrderBy
                state={[setStateULR]}
                map={[BackendMediaTypes, BackendMediaStyles, BackendMediaTags]}
              />
            }
          </div>
        </div>

        <div className="Content-App">
          <div className="content-midia-views">
            {
              BanckendMediaData &&
              BanckendMediaData.map(i => (
                <Link key={i.id} to={`/midiaview?midiaID=${i.id}`}>
                  <img src={i.image} alt={`ImageId: ${i.id}`} />
                </Link>
              ))
            }

          </div>
          <div className="Content-Pagination">
            <Pagination
              onChange={(_, v) => { setStateULR(state => ({ ...state, offset: v })) }}
              page={stateURL.offset}
              className={classes.root}
              count={tamanhoDaPaginação}
              boundaryCount={2}
              renderItem={(item) => (
                <PaginationItem
                  style={{
                    fontSize: 12,
                    fontWeight: 'bold',
                  }}
                  {...item}
                />
              )
              }
            />
          </div>
        </div>
      </div>
    </div>
  )
}