import React from 'react'
import Accordion from '../Accordion'
import Profile from '../Profile'
// import { ModalProfile } from '../Modals/Profile'

import './style.css'

const defaultValues = () => {
  const DefaultData = {
    name: '',
    email: '',
    isAdmin: false,
    AdminToken: '',
  };
  localStorage.setItem('@greenishBook:USER_DATA', JSON.stringify(DefaultData))
}

function AsideBar() {
  const useData = localStorage.getItem('@greenishBook:USER_DATA')
  const JsonUserData = JSON.parse(useData)

  const nome = JsonUserData ? JsonUserData.name : ''
  const email = JsonUserData ? JsonUserData.email : ''

  return (
    <div className="AsideBar">
      <Profile email={email} nome={nome}></Profile>

      <div className="category">
        {/* <a href="#"><span><img src="./assets/images/user.png" alt="Usuario" /> <ModalProfile /></span></a> */}

        <Accordion title="Adicionar" />

        <a href="/register">
          <p><img src="./assets/images/uderAdd.png" alt="User Add" />  Adicionar Usuario</p>
        </a>
        <a
          href="/#"
          onClick={() => defaultValues()}
        >
          <p><img src="./assets/images/logout.png" alt="Log out" />Logout</p>
        </a>
      </div>

      <div className="logo">
        <img src="./assets/images/logo.png" alt="Greenish" />
      </div>
    </div>
  )
}

export default AsideBar