import React from 'react'
// import { ModalBase } from '../ModalBase'
import "./style.css"
import { useState, useEffect } from 'react';
import { ImgRender } from '../../ImgRender';

import api from '../../../services/api';


const SelectionModalBase = (props) => {
  const [isVisible, setIsVisible] = props.MenuClose

  const handleOutsideClick = (e) => {
    if (e.target.id === 'modal') {
      setIsVisible(false)
    }
  }

  return (
    <div>
      {/* Botão ativação de modal */}
      <button
        className="ButtonModal-Selection-Midia"
        onClick={() => setIsVisible(!isVisible)}
      >
        {props.title}
      </button>

      {/* Render Modal */}
      {isVisible ?
        (
          <div
            id='modal'
            className='selection-ModalBase'
            onClick={(e) => handleOutsideClick(e)}
          >
            <div className="ContainerModalBase">

              <div className="Selection-headerModalBase">
                <span>{props.subtitle}</span>


              </div>

              <div className="Content">
                {props.children}
              </div>

            </div>

          </div>
        )
        : null}

    </div>
  )
}

export const SelectionMidia = (props) => {
  const [dataToBook, setDataToBook] = props.dataa
  const [imgs, setImgs] = useState([])

  const [value, setValue] = props.dataa

  const [closeMenuu, setCloseMenuu] = useState(false)

  useEffect(() => setValue(dataToBook), [dataToBook, imgs])

  const fetchMidias = () => {
    api.get("/media/")
      .then(response => {
        let res = response.data
        setImgs(res)
      })
      .catch(err => {
        console.error("Ops!! Verifique se o Banco de dados esta ativo. " + err);
      })
  }
  useEffect(() => fetchMidias(), [])

  useEffect(() => {
    if (dataToBook == 0) {
      fetchMidias()
    }
  }, [dataToBook])

  return (
    <SelectionModalBase title="Adicionar Midias" subtitle='Imagens Selecionadas: ' MenuClose={[closeMenuu, setCloseMenuu]}>
      <div className="SelectionMidias">
        <div className="Content" >
          <p>Midias Para Selecionar</p>
          <div className="Midias" >
            {
              imgs.map((image, index) => {
                return (
                  <ImgRender
                    key={index}
                    data={image}
                    func={() => {
                      setDataToBook([...dataToBook, image])
                      imgs.splice(index, 1)
                      setImgs([...imgs])
                    }}
                  />
                )
              })
            }
          </div>
        </div>
        <div className="Content">
          <p>Midias Selecionadas</p>
          <div className="Midias">
            {
              dataToBook.map((imageToBook, indexToBook) => {
                return (
                  <ImgRender
                    key={indexToBook}
                    data={imageToBook}
                    func={() => {
                      setImgs([...imgs, imageToBook])
                      dataToBook.splice(indexToBook, 1)
                      setDataToBook([...dataToBook])
                      // setValue([...dataToBook])
                    }}
                  />
                )
              })
            }
          </div>
        </div>
        <div className=""></div>
        <button className="Buttons" onClick={() => {
          setCloseMenuu(false)
        }}>Salvar</button>
      </div>
    </SelectionModalBase>
  )
}